import * as React from 'react'
import * as ReactDOM from 'react-dom'
import {boltCall} from '../Share'
import {SortByName} from '../Share'
import {ProductListItem} from './ProductListItem'
//
//area to hold product suggestions
//

export class ProductListPanel extends React.Component<any,any> {
    private _el:HTMLElement;
    constructor(){
        super();
        this.state = {"products":[{
                        "previewImage":"",
                        "name":""
        }]};
    }
    
    render(){
        return <div class >
                <div className={"w3-center"+""} style={{fontSize:'150%'}}>You might like...</div>
                {this.state.products.map((v:any, i:number)=>{
                    return <div><ProductListItem item={v} index={i}/></div>
                })}
            </div>
    }
    
    componentDidMount = () => {
        boltCall("mobile/getRecommendedProducts", {userId:"someuser"}, (result:any) => {
        result.return_value.products.sort(SortByName);
            this.setState(result.return_value);
        });
    }
}





