import * as React from 'react'
import * as ReactDOM from 'react-dom'

//Note: This is using hard coded simple auth. HMAC auth is recommended for production and requires some crypto
var boltUrlBase:string = "http://"+window.location.hostname+":8888/";
var username:string = "publicweb";
var userkey:string = "webaccess1";
var retryMs:number = 1000;

export function boltCall(apicall:string, payload:any, resultcb:(result:any)=>void) {
    let request = new XMLHttpRequest();
    let URL = boltUrlBase+"request/"+apicall;
    let data = JSON.stringify(payload);
    request.open("POST", URL, true);
    request.setRequestHeader("Authorization", "Basic " + btoa(username + ":" + userkey))
    request.send(data);
    request.onload = function() {
        if (request.status >= 200 && request.status < 400) {
            // Success!
            let resp = JSON.parse(request.response);
            if (resp.complete) {
                resultcb(resp);
            } else {
                boltRepeatCall(resp.id, resultcb, 1);
            }
        } else {
            console.log("Error with boltCall");
        } 
    }
}

export function boltRepeatCall(id:string, resultcb:(result:any)=>void, attempt:number) {
    setTimeout(()=> {
        let request = new XMLHttpRequest();
        let URL = boltUrlBase+"retr/peek/"+id;
        let data = ("{}");
        request.open("POST", URL, true);
        request.setRequestHeader("Authorization", "Basic " + btoa(username + ":" + userkey))
        request.send(data);
        request.onload = function() {
            if (request.status >= 200 && request.status < 400) {
                // Success!
                let resp = JSON.parse(request.response);
                if (resp.complete) {
                    resultcb(resp);
                } else {
                    boltRepeatCall(resp.id, resultcb, attempt+1);
                }
            } else {
                console.log("Error with boltRepeatCall");
            } 
        }
    }, retryMs);
}

export function SortByName(a:any,b:any){
    return (a.name.toUpperCase() < b.name.toUpperCase()) ? -1 : (a.name.toUpperCase() > b.name.toUpperCase()) ? 1 : 0;
}
export function ReverseSortByName(a:any,b:any){
    return (a.name.toUpperCase() < b.name.toUpperCase()) ? 1 : (a.name.toUpperCase() > b.name.toUpperCase()) ? -1 : 0;
}
export function SortByPrice(a:any,b:any){
    return (a.price < b.price) ? 1 : (a.price > b.price) ? -1 : 0;
}
export function ReverseSortByPrice(a:any,b:any){
    return (a.price < b.price) ? -1 : (a.price > b.price) ? 1 : 0;
}
export function SortByQTY(a:any,b:any){
    return (a.qty < b.qty) ? 1 : (a.qty > b.qty) ? -1 : 0;
}
export function ReverseSortByQTY(a:any,b:any){
    return (a.qty < b.qty) ? -1 : (a.qty > b.qty) ? 1 : 0;
}
export function SortByDate(a:any,b:any){
    let dateA = new Date(a.date);
    let dateA_utc = new Date(dateA.getUTCFullYear(), dateA.getUTCMonth(), dateA.getUTCDate(),  dateA.getUTCHours(), dateA.getUTCMinutes(), dateA.getUTCSeconds());
    let dateB = new Date(b.date);
    let dateB_utc = new Date(dateB.getUTCFullYear(), dateB.getUTCMonth(), dateB.getUTCDate(),  dateB.getUTCHours(), dateB.getUTCMinutes(), dateB.getUTCSeconds());
    return dateA_utc as any - (dateB_utc as any);
}
export function ReverseSortByDate(a:any,b:any){
    let dateA = new Date(a.date);
    let dateA_utc = new Date(dateA.getUTCFullYear(), dateA.getUTCMonth(), dateA.getUTCDate(),  dateA.getUTCHours(), dateA.getUTCMinutes(), dateA.getUTCSeconds());
    let dateB = new Date(b.date);
    let dateB_utc = new Date(dateB.getUTCFullYear(), dateB.getUTCMonth(), dateB.getUTCDate(),  dateB.getUTCHours(), dateB.getUTCMinutes(), dateB.getUTCSeconds());
    
    return dateA_utc < dateB_utc ? 1 : dateA_utc > dateB_utc ? -1 : 0;
}

//regex to put commas in proper positions, and go to 2 decimal places
export function Currency (s:number)  {
        if (s){
            var ss = s.toFixed(2).toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
            return "$"+ss 
        }
    }
    
//shows list based on the data-id that is  passed in
export function ShowHide(selector:string){
    var x = document.querySelector(selector);
    if (x.className.indexOf("w3-show") == -1){
        x.className += " w3-show";
    } else {
        x.className = x.className.replace(" w3-show", "");
    }
}


