import * as React from 'react'
import * as ReactDOM from 'react-dom'
import {boltCall} from '../Share'
import {ShowHide} from '../Share'

export class Promo extends React.Component<any,any> {
    private _el:Element;
    constructor(){
        super();
        this.state = {}
    }
    render(){//displays a text box and a hidden promo code indicator
        return <div className="promoField" ref={(c:Element) => this._el = c}>
                <input style={{maxWidth:'80%'}}id="Promo" type="text" placeholder="Enter Promo Code"></input>
                <br/>
                <div  className="w3-green w3-hide" id="promoLight">Promo accepted!</div><br/>
            </div>;
    }
    //sets a keydown listener for the promocode
    componentDidMount = () => {
        this._el.addEventListener("keydown", this.firePromo.bind(this));
    }
    timer = 2000;// 2 seconds after keydown
    timeout = 0;
    //starts timeout function that calls promoCall
    firePromo = (e:Event) => {
        document.getElementById("Promo").onkeydown =  () => {
            clearTimeout(this.timeout);
            this.timeout = setTimeout(this.promoCall, this.timer);
        }
    }
    //fires off a bolt call after timer is up.
    promoCall = () => {
        boltCall("mobile/getPromo",{},(result:any) => {
            ShowHide("#promoLight");
            //toggleHidden("promoLight");
        });
    }
}
//used in CartPopover as well to toggle visibility of elements
export function toggleHidden(id:string){
    var elem = document.getElementById(id)
        if (elem.className == ""){
            elem.className = "hidden";
        } else {
            elem.className = "";
        }
}


