import * as React from 'react'
import * as ReactDOM from 'react-dom'
import {Currency} from '../Share'

//
//Panel that shows the cart summary
//

export interface Props { cartData:any}

export class CartDataPanel extends React.Component<any,any>{
    constructor(){
        super();
    }
    
    render() {
        return <div className={"w3-card w3-round"+""}>
                    <div className="w3-row">
                        <div className="w3-col s6">Subtotal: </div>
                        <div className="w3-right-align w3-col s4">{Currency(this.props.cartData.subtotal)}</div>
                        <div className="w3-col s2"/>
                    </div>
                    <div className="w3-row">
                        <div className="w3-col s6">Shipping: </div>
                        <div className="w3-right-align w3-col s4">{Currency(this.props.cartData.shipping)}</div>
                        <div className="w3-col s2"/>
                    </div>
                    <div className="w3-row">
                        <div className="w3-col s6">Tax: </div>
                        <div className="w3-right-align w3-col s4">{Currency(this.props.cartData.tax)}</div>
                        <div className="w3-col s2"/>
                    </div>
                     <div className="w3-row w3-border-top">
                        <div className="w3-col s6">Total: </div>
                        <div className="w3-right-align w3-col s4">{Currency(this.props.cartData.total)}</div>
                        <div className="w3-col s2"/>
                     </div>
                    </div>
    }
}




