import * as React from 'react'
import * as ReactDOM from 'react-dom'

//
//banner to be displayed at the top of the cart
//

export interface Props {images:any}

export class HomeBanner extends React.Component<any,any>{
    private _el:Element;
    constructor(){
        super();
        this.state = {};
    }
    
    render(){
        return <div ref={(component:Element) => this._el = component}>
                <img  src={this.props.images[1]}/>
            </div>
    }
    
    componentDidMount = () => {
    }
}


