import * as React from 'react'
import * as ReactDOM from 'react-dom'
import {boltCall} from '../Share'
import {Button} from './Button'
import {Popper} from './Popper'
import {Currency} from '../Share'
import {ShowHide} from '../Share'

//cart items are created dynamicall in cart panel, using values from cartPanel state.
//accept prop of cart item data
export interface Props { item:any, index:number, theme:any, colors:any }
export class CartItem extends React.Component<any,any>{
    private _el: Element;
    constructor(){
        super();
    }
    
    render(){
        return <div style={{height:'100%'}} className={"w3-card w3-round-large w3-center w3-hover-shadow"+""} ref={(component:Element) => this._el = component}>
                <div className="w3-row" style={{height:'100%'}}>
                    <div className="w3-col s2 m2" style={{height:'100%'}}>
                        <img id="thumb" src={this.props.item.previewImage} style={{height:'80px',width:'80px'}}  className='w3-circle'/> 
                        <Popper index={this.props.index}colors={this.props.colors}style={{display:'none'}} item={this.props.item}/>
                    </div>
                    <div className="w3-col s4 m4" style={{height:'100%'}}>
                        {this.props.item.name}
                        <br/>
                        {this.props.item.sku}
                    </div>
                    <div className="w3-col s2 m2" style={{height:'100%'}}>
                        
                        <Button buttonID="remove" dataID={this.props.index} buttonText="remove" colors={this.props.colors}></Button>
                        <Button buttonID="update" dataID={this.props.index} buttonText="update" colors={this.props.colors}></Button>
                    </div>
                    <div className="w3-col s2 m2" style={{height:'100%'}}>
                        <div id="price" className="itemRightTop">{Currency(this.props.item.price)}</div>
                        <br/>
                        <input style={{maxWidth:'55%'}} id="qty" value={this.props.item.qty} className="qtyField" data-id={this.props.index} placeholder="qty" type="text" />
                    </div>
                    <div className="w3-col s2 m2" style={{height:'100%'}}>
                        <div style={{height:'100%'}}>
                            {this.props.item.date}
                        </div>
                    </div>
                </div>
            </div>
    }
    
    componentDidMount = () => {
        //add mouse hover listener to cartitems thumbnail image
        let thumb = this._el.querySelector("#thumb") as any;
        thumb.onmouseover = () => {
            //this.popUp();
            ShowHide("#popContainer"+this.props.index);
        }
        thumb.onmouseout = () => {
            //this.popAway();
            ShowHide("#popContainer"+this.props.index);
        }
    }
    //shows image and description when hovering over cart item picture
    popUp = () => {
        let e = this._el.querySelector('#popContainer') as any;
        e.style.display = 'block';
    }
    //removes the popup when mouse is not hovering over the cartitems image
    popAway = () => {
        let e = this._el.querySelector('#popContainer') as any;
        e.style.display = 'none';
    }
    
    
}


