import * as React from 'react'
import * as ReactDOM from 'react-dom'
import {boltCall} from '../Share'
import {Currency} from '../Share'

//
//ProductListItem is the element used to display product suggestions
//

export interface Props {item:any}
export class ProductListItem extends React.Component<any,any> {
    private _el:Element;
    constructor(){
        super();
    }
    
    render(){
        return <div className={"w3-card w3-round-medium w3-hover-shadow"+""} ref={(component:Element) => this._el = component}>
                <div className="productTop">
                    <div className="productImage w3-center w3-padding">
                        <img  className="w3-round-large" style={{maxHeight:'95%',maxWidth:'95%'}} src={this.props.item.previewImage} />
                    </div>
                </div>
                <div className="">
                    <div className="w3-row">
                        <div className="w3-col s7 l8 w3-center">{this.props.item.name} </div>
                        
                        <div  className="w3-col s5 l4"> <div classname="centerPrice">{Currency(this.props.item.price)}</div></div>
                    </div>
                    <div className="w3-row">
                        <div className="w3-col s7 w3-center">{this.props.item.sku}</div>
                        <div className="w3-col s5 w3-center">{this.props.item.stock} in stock</div>
                    </div>
                </div>
            </div>
    }
    
    componentDidMount = () => {
        //when the product suggestion is clicked go to the clients product page
        this._el.addEventListener("click", this.productLinker);
    }
    productLinker = () => {//navigate to the clients product page
        window.location = this.props.item.previewImage;
    }
}


