import * as React from 'react'
import * as ReactDOM from 'react-dom'
import {CartItem} from './CartItem'
import * as Share from '../Share'
import {Promo} from './Promo'
import {CartDataPanel} from './CartDataPanel'
import {CartCheckoutPanel} from './CartCheckoutPanel'
import * as CDP from './CartDataPanel'
import {ProductListItem} from './ProductListItem'
import {ProductListPanel} from './ProductListPanel'
import {HomeBanner} from './HomeBanner'
import {SortPanel} from './SortPanel'
import {DropDown} from './DropDown'
import {HideList} from './DropDown'
import {ShowList} from './DropDown'
import {Popper} from './Popper'
import {ThemeSelector} from './ThemeSelector'

export interface Props { }
//main cart display area
export class CartPanel extends React.Component<any,any>{
    private _el:Element;
    private selectors = ["cart","wish"]
    
    constructor(){
        super();
        this.state = {
            "images":[],
            "cartItems":[], 
            "wishItems":[],
            "subtotal":0,
            "shipping":0,
            "tax":0,
            "total":0,
            "promos":[],
            "discount":0,
            "theme":{},
            "qty":0,
            "nameToggle":false,
            "priceToggle":false,
            "qtyToggle": false,
            "dateToggle": false,
            "colors":{
            },
            "DropListItems":{
                "Sort":[]
            },
            "ThemeList":[]
        };
    }
    
    render(){
        return <div className="" style={{height:'100%'}} ref={(component:HTMLElement) => this._el = component}>
                <div className="w3-row">
                    <div id="b" className={"w3-col w3-center"+this.state.colors.color3} style={{height:'100%'}}>
                        <HomeBanner images={this.state.images}/>
                    </div>
                </div>
                <div className={"w3-row" + ""} style={{height:'100%'}}>
                    <div className={"w3-col m3 l2 w3-center"+this.state.colors.color2} style={{height:'100%'}}>
                        <div className={this.state.colors.color3}>
                            <div style={{fontSize:'150%'}} >
                                Summary
                            </div>
                            <ThemeSelector ThemeList={this.state.ThemeList}/>
                        </div>
                        <CartDataPanel cartData={{subtotal:this.state.subtotal, tax:this.state.tax, shipping:this.state.shipping, stock:this.state.stock, discount:this.state.discount, total:this.state.total}} />
                        <CartCheckoutPanel colors={this.state.colors}/>
                        <br/>
                        <Promo/>
                        <div className={"w3-hide-small"+""}>
                            <ProductListPanel/>
                        </div>
                    </div>
                    <div className={"w3-col m7 l8 w3-center"+this.state.colors.color3} style={{height:'100%'}}>
                        <div style={{fontSize:'150%'}} >
                            Cart
                        </div>
                        <DropDown DropListItems={this.state.DropListItems.Sort} selector="cart" dropName="Sort By: "/>
                        {this.state.cartItems.map((v:any, i:any)=>{
                            return <div className={"w3-center"+this.state.colors.color1} style={{height:'15%'}} ><CartItem item={v} colors={this.state.colors} index={i} key={i}></CartItem></div>
                        })} 
                    </div>
                    <div className={"w3-col m2 w3-center"+this.state.colors.color3} style={{height:'100%'}}>
                        <div className="w3-hide-medium w3-hide-large" style={{height:'100%',fontSize:'150%'}}>
                            
                            <br/>
                        </div>
                        <span style={{height:'100%',fontSize:'150%'}}>Wishlist</span>
                        <br/>
                        <DropDown DropListItems={this.state.DropListItems.Sort} selector="wish" dropName="Sort By: "/>
                        {this.state.wishItems.map((v:any, i:any)=>{
                            return <div className={this.state.colors.color2} ><ProductListItem style={{height:'100%'}} item={v} colors={this.state.colors} index={i} key={i}></ProductListItem></div>
                        })}
                    </div>
                </div>
            </div>
    }
    componentDidMount = () => {
        //set html background color
        document.documentElement.className = this.state.colors.color3;
        
        this._el.addEventListener('click', this.clickHandler); //assign main click listener
        //get the main banner
        Share.boltCall("mobile/getHomeBanners", {}, (result:any)=> {
            this.setState(result.return_value);
        });
        //get cart data
        Share.boltCall("mobile/adjustCart", {cartId: 123, product: "bob"}, (result:any) => {
            this.setState(result.return_value);
        });
        
        //get color themes for dropdown and color scheme
        Share.boltCall("mobile/getThemeNormal", {}, (result:any)=>{
            this.setState(result.return_value);
        })
    }
    
    // clickHandler executes when any click event occurs on the cart panel
    clickHandler = (ev:Event) => {
        let ex = ev.target as any; //get the element that was clicked
        switch (ex.id){//use the elements id as the switch
            case "submitButton":{
                this.sendCall("mobile/submitCart", {});
                break;
            } case "cancelButton":{
                this.sendCall("mobile/cancelCart",{});
                break;
            } case "clearButton":{
                this.sendCall("mobile/clearCart",{});
                break;
            } case "remove":{//send bolt call with the item sku to be removed
                this.sendCall("mobile/remove",this.state.cartItems[ex.getAttribute("data-id")].sku );
                ex.parentElement.parentElement.parentElement.parentElement.parentElement.remove();//remove the item element
                break;
            } case "update":{//sends bolt call to adjust cart
                //assigns the clicked cartItem to temp
                let temp = this.state.cartItems[ex.getAttribute("data-id")];
                //moves to higher level of cartItem and looks for the qtyField className
                let tempQty = ex.parentElement.parentElement.parentElement.querySelector(".qtyField") as any;
                temp.qty = tempQty.value;//assigns the value from the user editable qty field to the qty in the desired cartItems
                this.sendCall("mobile/adjustCart", temp);//sends the cartItem with the newly edited qty
                break;
            } case "qty":{//add listener to qty field, to listen for the enter key
                ex.parentElement.addEventListener("keydown", this.qtyHandler.bind(this,ex) as any );
                break;
            } case "nameSort":{//checks the state of the toggle then sorts items alphabetically
                if (!this.state.nameToggle){//if nameToggle = false
                    this.sortBy(Share.SortByName, ex.getAttribute("data-id"));
                    this.setState({nameToggle:true});//switch toggle
                    this.upArrow(ex);
                } else {//if nameToggle = true
                    this.sortBy(Share.ReverseSortByName, ex.getAttribute("data-id"));
                    this.setState({nameToggle:false});
                    this.downArrow(ex);
                }
                HideList(this.selectors);
                this.selectors = [];
                break;
            } case "priceSort":{//checks the state of the toggle then sorts items by price
                if (!this.state.priceToggle){
                    this.sortBy(Share.SortByPrice, ex.getAttribute("data-id"));
                    this.setState({priceToggle:true});
                    this.upArrow(ex);
                } else {
                    this.sortBy(Share.ReverseSortByPrice, ex.getAttribute("data-id"));
                    this.setState({priceToggle:false});
                    this.downArrow(ex);
                }
                HideList(this.selectors);
                this.selectors = [];
                break;
            } case "qtySort":{//checks the state of the toggle then sorts items quantity in cart
                if (!this.state.qtyToggle){
                    this.sortBy(Share.SortByQTY, ex.getAttribute("data-id"));
                    this.setState({qtyToggle:true});
                    this.upArrow(ex);
                } else {
                    this.sortBy(Share.ReverseSortByQTY, ex.getAttribute("data-id"));
                    this.setState({qtyToggle:false});
                    this.downArrow(ex);
                }
                HideList(this.selectors);
                this.selectors = [];
                break;
            } case "dateSort":{//checks the state of the toggle then sorts items by date
                if (!this.state.dateToggle){
                    this.sortBy(Share.SortByDate, ex.getAttribute("data-id"));
                    this.setState({dateToggle:true});
                    this.upArrow(ex);
                } else {
                    this.sortBy(Share.ReverseSortByDate, ex.getAttribute("data-id"));
                    this.setState({dateToggle:false});
                    this.downArrow(ex);
                }
                HideList(this.selectors);
                this.selectors = [];
                break;
            } case "dropMenu":{//when dropdown box is clicked
                //add item to selectors toggles
                this.selectors.push(ex.getAttribute("data-id") as string);
                ShowList(ex.getAttribute("data-id"));//shows list based on its data-id
                break;
            } case "theme":{//when a item in the theme dropdown is clicked
                //send a bolt call that uses data-name to get the desired theme
                this.changeTheme(ex.getAttribute('data-name'));
                HideList(this.selectors);
                this.selectors = [];
                break;
            } default:{
                HideList(this.selectors);
                this.selectors = [];
                //will happen on every mouse click that is not specified.
            } 
        }
    }
    
    //Wrapper for boltcall.
    sendCall = (apiCallName:string, payload?:any) => {
        Share.boltCall(apiCallName, payload, (result:any)=>{
            alert(result.return_value.message);
        })
    }
    
    //Fires when a key is pressed in the qty field, after it has been clicked. Listens for the enter key
    qtyHandler = (ex:any,ev:any) => {
        if (ev.keyCode === 13){//If 'enter' is pressed
            let temp = this.state.cartItems[ex.getAttribute("data-id")]
            temp.qty = ex.value;//Assign the user entered qty to temp.qty
            Share.boltCall("mobile/adjustCart", temp,(result:any)=>{
                alert(result.return_value.message);
                ex.removeEventListener("keyDown", this.qtyHandler);
            });
        } 
    } 
    //css is passed in and appended to document head.
    changeStyle = (theme:any) => {
        let css = theme;
        let style = document.createElement('style') as any;
        if (css !== ""){
            if (style.styleSheet){//2 posibiliteis exist depending on client borwser.
                style.styleSheet.cssText = css;
            } else{
                style.appendChild(document.createTextNode(css));
            }
        } else {
            console.log("css is empty");
        }
        //add the style element to the head
        document.getElementsByTagName('head')[0].appendChild(style);
    }
    //sortBy passes in a sorting function and  a data id of the clicked button
    sortBy = (sort?:any, selector?:any) => {
        Share.boltCall("mobile/adjustCart", {cartId: 123, product: "bob"}, (result:any) => {
            switch (selector) {
                case "cart":{//sort cart
                    result.return_value.cartItems.sort(sort);
                    break;
                } case "wish":{//sor wishlist
                    result.return_value.wishItems.sort(sort);
                }
            }
            this.setState(result.return_value); 
        });
    }
    //places up arrow next to a button to indicate wich direction the list is sorted
    upArrow = (ex:any) => {
        //remove all existing arrows
        let x = ex.parentElement.parentElement.querySelectorAll(".arrow");
        for (let i=0;i < x.length; i++ ){
            x[i].innerHTML = "";
        }//put an arrow next to the clicked button based on id
        ex.parentElement.parentElement.querySelector("#"+ex.id+"Arrow").innerHTML = '<img src="upArrow.png"/>';
    }//places a down arrow next to a button
    downArrow = (ex:any) => {
        let x = ex.parentElement.parentElement.querySelectorAll(".arrow");
        for (let i=0;i < x.length; i++ ){
            x[i].innerHTML = "";
        }
        ex.parentElement.parentElement.querySelector("#"+ex.id+"Arrow").innerHTML = '<img src="downArrow.png"/>';
    }
    componentDidUpdate = () => {//set height to 100% for scaling reasons
        document.getElementById("fairmont-cart-container").style.height = '100%';
        //document.documentElement.className = this.state.colors.color1;
    }
    //gets the new theme and sets it to the state
    changeTheme = (name:string) => {
        Share.boltCall("mobile/getTheme"+name, {}, (result:any)=>{
            this.setState(result.return_value);
        })
    }
}   


