import * as React from 'react'
import * as ReactDOM from 'react-dom'

//
//Prototype for dropdown menus
//

export interface Props {DropListItems:any,selector?:string,dropName:any,dataID?:string}

export class DropDown extends React.Component<any,any>{
    private _el:HTMLElement;
    
    constructor(){
        super();
    }
    
    render(){
        return <div id="h" ref={(component:HTMLElement) => this._el = component} className="w3-dropdown-click">
                <button id="dropMenu" data-id={this.props.selector} className="w3-btn w3-light-gray">{this.props.dropName}</button>
                <div id={"list" + this.props.selector} style={{}} className="w3-center w3-dropdown-content w3-card">
                    {this.props.DropListItems.map((v:any, i:any)=>{
                            return <a href="#" data-id={this.props.selector} data-name={v.listTitle} id={v.listID} >{v.listTitle} <div id={v.listID+"Arrow"} className="arrow" style={{display:'inline-block'}}></div></a>
                        })}
                </div>
            </div>          
    }
}
//shows list based on the data-id that is  passed in
export function ShowList(selector:string){
    var x = document.querySelector("#list"+selector);
    if (x.className.indexOf("w3-show") == -1){
        x.className += " w3-show";
    } else {
        x.className = x.className.replace(" w3-show", "");
    }
}
//hides all open drop down lists
export function HideList(selectors:string[]){
    for (let i = 0; i < selectors.length; i++){
        var x = document.querySelector('#list'+selectors[i]);
        console.log(x);
        if (x.className.indexOf("w3-show") != -1){
            x.className = x.className.replace(" w3-show", "");
        }
    }
}


