import * as React from 'react'
import * as ReactDOM from 'react-dom'

//
//Prototype for popup bubbles
//

export interface Props {item:any, colors:any, index:number}
export class Popper extends React.Component<any,any>{
    private _el:HTMLElement;
    constructor(){
        super();
    }
    
    render(){
        return <div style={{maxWidth:'100%'}}  id={"popContainer"+this.props.index} className={'w3-card'+this.props.colors.color3} ref={(component:any) => this._el = component}>
            <div className={this.props.colors.color2} style={{maxWidth:'90%'}}><img style={{width:'100%'}} src={this.props.item.previewImage}/></div>
            <div className={this.props.colors.color2} style={{ textAlign:'center',padding:'2px',  overflow:'hidden', wordWrap:'break-word', maxWidth:'100%'}} id="description" innerHTML={this.props.item.description} >{this.props.item.description}</div>
            </div>
    }
    componentDidMount = () => {
        this._el.style.position = 'absolute';
        this._el.style.zIndex = '3';
        this._el.className = 'w3-hide';
        this._el.style.transform = 'translate(-100%, -100%)';
    }
    componentDidUpdate = () => {
        this._el.className = 'w3-hide '+this.props.colors.color3;
    }
}


