import * as React from 'react'
import * as ReactDOM from 'react-dom'

//
//Prototype for all buttons
//

export interface Props {buttonID:string, buttonText:string, dataID:any, colors:any}

export class Button extends React.Component<any,any>{
    
    constructor(){
        super();
    }
    
    render(){
        return <div style={{padding:'0.5em'}} >
                <div style={{fontSize:"80%"}} className={" w3-btn-block w3-tiny w3-round"+this.props.colors.colorBtn}>
                    <div id={this.props.buttonID} data-id={this.props.dataID}>{this.props.buttonText} </div>
                </div>
            </div>
    }
    
    componentDidMount = () => {
    }
}


