import * as React from 'react'
import * as ReactDom from 'react-dom'
import {DropDown} from './DropDown'

//
//theme selection dropdown menu
//maybe not worth its own element, but it already exists.

export interface Props {ThemeList:any,dataID?:any}

export class ThemeSelector extends React.Component<any,any>{
    
    constructor(){
        super();
    }
    
    render(){
        return <div>
                <DropDown DropListItems={this.props.ThemeList} selector="theme" dropName="Themes: "/>
            </div>
    }
}


