import * as React from 'react'
import * as ReactDOM from 'react-dom'
import {Button} from './Button'

//
//Panel that holds checkout buttons
//

export interface Props {colors:any}

export class CartCheckoutPanel extends React.Component<any,any>{
    
    constructor(){
        super();
    }
    
    render(){
        return <div><div className="w3-row w3-center">
                <div className="w3-padding w3-col s12 w3-center">
                <Button buttonID="cancelButton" buttonText="Cancel" colors={this.props.colors}></Button>
                </div>
                <div className="w3-padding w3-col s12 w3-center">
                <Button buttonID="clearButton" buttonText="Clear" colors={this.props.colors}></Button>
                </div>
                <div className="w3-padding w3-col s12 w3-center">
                <Button buttonID="submitButton" buttonText="Checkout" colors={this.props.colors}></Button>
                </div>
            </div></div>
    }
}


